*{
    padding: 0;
    margin:0;
}

a{
    text-decoration: none;
}

body{
    background-color:#DCDCDC;
}

.header{
    width:100%;
    height: 50px;
    background-color: #FF6347;
    background-image: url(../assets/taobao.png);
    background-repeat: no-repeat;
    background-size: 50px;
    position: fixed;
    z-index: 1;
}
.searchBar{
    width: 300px;
    height: 30px;
    margin-top: 10px;    
    margin-left: 50px;
    border: 1px solid  	#FF6347;
    background-color: #FF4500;
    border-radius: 5px;
    text-align: center;
    color:white;
}

.lunbotu{
    width: 100%;
    padding-top: 50px;
}

.like{
    color: #FF4500;
    text-align: center;
    height: 30px;
    font-size: large;
    line-height: 30px;
}

.content{
    width:100%;
}

.contentList li{
    border-radius: 10px;
    display: inline-block;
    margin: 0 auto;
    height: 200px;
    width: 170px;
    margin-left: 10px;
    background-image: url(../assets/item.jpg);
    background-color: white;
    background-repeat: no-repeat;
    background-size: 80%;
    background-position: 20px 10px;
    position: relative;
}

.item{
    position: absolute;
    bottom: 0px;
    margin-left: 5px;
}